<template>
  <div id="app">
   
    <div class="allContainer">
      <div class="left">
        <header>
          <div class="border">
            <Row >
              <Col span="2">
              <div>
                <Avatar :style="{background: color}">{{ user.name }}</Avatar>
              </div>
              </Col>
              <Col span="16">
              <div class="font">
                <h2>{{user.name}}</h2>
              </div>
              </Col>
                <Col span="6">
              <div class="font">
                <Button @click="toL" long v-show="!logged">Login</Button>
                <Row>
                  <Col span="12"><Button @click="show" long v-show="logged" type="success"></Button> </Col>
                   <Col span="12"><Button @click="exitT" long v-show="logged" type="error"></Button> </Col>
                   
                </Row>
                
              </div>
              </Col>
              
            </Row>
          </div>
        </header>
        <markdown 
            :mdValuesP="user.mdValue"  
            :fullPageStatusP="false" 
            :editStatusP="true" 
            :previewStatusP="true" 
            :navStatusP="true"
            :icoStatusP="true"  
            @childevent="childEventHandler"
            ></markdown>
        
      </div>
      <transition name="sideDonghua">
        <div class="right" v-show="ifShowInfo">
          <div class="border">
            <Row :gutter="16">
              <Col span="6">
              <div>
                <Avatar :style="{background: color}">{{ user.name }}</Avatar>
              </div>
              </Col>
              <Col span="18">
              <div class="font">
                <h2>{{user.name}}</h2>
              </div>
              </Col>
            </Row>
            </div>
        <Card  v-for="(i,inx) in arr" :key="inx">
            <p slot="title"> {{i.name}}</p>
            <p>{{i.mdValue.split('\n')[0]}} </p>
            <Button @click="chaTo(i.mdValue,inx)" long type="info">
                </Button>
                            <Button @click="save(i.mdValue,inx)" long type="primary">
                </Button>
                        <Button @click="delet(inx)" long type="error">
                </Button>
        </Card>
          <Button @click="modal10=true" long>
                </Button>
                    <Modal
        title=""
        v-model="modal10"
        class-name="vertical-center-modal">
       <Input search enter-button="Search" placeholder="Enter something..."  v-model="inputval"/>
       <Button @click="creatA" long>
               Creat</Button>
    </Modal>
        </div>
      </transition>
      <transition name="loginDonghua">




    <div class="middle" v-show="ifLogin">


     <div v-show="!toReg">
       <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
        <FormItem prop="user">
            <Input type="text" v-model="formInline.user" placeholder="Username">
                <Icon type="ios-person-outline" slot="prepend"></Icon>
            </Input>
        </FormItem>
        <FormItem prop="password">
            <Input type="password" v-model="formInline.password" placeholder="Password">
                <Icon type="ios-lock-outline" slot="prepend"></Icon>
            </Input>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="LoginSubmit()">Signin</Button>
            <Button type="primary" @click="toR">Regiseter</Button>
        </FormItem>
    </Form></div>
      <!--  -->
      <div v-show="toReg">
                    
        <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
            <FormItem label="Name" prop="name">
            <Input type="text" v-model="formCustom.user" ></Input>
        </FormItem>
        <FormItem label="Password" prop="passwd">
            <Input type="password" v-model="formCustom.passwd"></Input>
        </FormItem>
        <FormItem label="Confirm" prop="passwdCheck">
            <Input type="password" v-model="formCustom.passwdCheck"></Input>
        </FormItem>
      <FormItem label="E-mail" prop="mail">
            <Input type="e-mail" v-model="formCustom.email"></Input>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleRegiseterSubmit()">Submit</Button>
            <Button type="ghost" @click="backTo" style="margin-left: 4px">Back</Button>
        </FormItem>
    </Form>



       </div>




    </div>



    </transition>
    
    </div>
 
  </div>
</template>
<script>
 let index=0;
 let buf = "";
export default {

  name: 'App',
   data() {
       const validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('Please enter your password'));
                } else {
                    if (this.formCustom.passwdCheck !== '') {
                        // 
                        this.$refs.formCustom.validateField('passwdCheck');
                    }
                    callback();
                }
            };
            const validatePassCheck = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('Please enter your password again'));
                } else if (value !== this.formCustom.passwd) {
                    callback(new Error('The two input passwords do not match!'));
                } else {
                    callback();
                }
            };
            const validateAge = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error('Age cannot be empty'));
                }
                // 
                setTimeout(() => {
                    if (!Number.isInteger(value)) {
                        callback(new Error('Please enter a numeric value'));
                    } else {
                        if (value < 18) {
                            callback(new Error('Must be over 18 years of age'));
                        } else {
                            callback();
                        }
                    }
                }, 1000);
            };




    //  
            return {
              inputval:"",
              modal10: false,
               ifShowInfo:false,
               ifLogin:false,
               toReg:true,
               logged:false,
              arr:[
                  { name: '',
                  mdValue:'## Vue-markdownEditor\n'},
                  { name: '',
                  mdValue:'## Vue-markdownEditor\n'}
                  ],
                user: {
                  name: '',
                  mdValue:''
                },
                color:'#f56a00',
                               formInline: {
                    user: '',
                    password: ''
                },
                ruleInline: {
                    user: [
                        { required: true, message: 'Please fill in the user name', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: 'Please fill in the password.', trigger: 'blur' },
                        { type: 'string', min: 4, message: 'The password length cannot be less than 6 bits', trigger: 'blur' }
                    ]
                },
                                 formCustom: {
                    passwd: '',
                    passwdCheck: '',
                    user: '',
                    email:''
                },
                ruleCustom: {
                    passwd: [ {  required: true, message: 'The password cannot be empty', trigger: 'blur' },
                        { validator: validatePass, trigger: 'blur' }
                        
                    ],
                    passwdCheck: [{  required: true, message: 'The password cannot be empty', trigger: 'blur' },
                        { validator: validatePassCheck, trigger: 'blur' }
                         
                    ],
                    name: [
                        {  required: true, message: 'The name cannot be empty', trigger: 'blur' }
                    ],
                    mail: [
                     
                        { type: 'email', message: 'Incorrect email format', trigger: 'blur' }
                        
                    ]
                    
                }
            }
            
        },
    methods:{
                  childEventHandler:function(res){
                // resdata,mdValuehtmlValue
                //this.arr[index].mdValue=res.mdValue
                buf=res.mdValue
               // console.log(res);
                console.log(buf+' 2')
            },
            exitT:function(){
                  window.location.reload()
            },
            creatA:function(){
                    this.modal10=true;
                    let b ={ name: this.inputval,
                  mdValue:'## Vue-markdownEditor\n'};
                  this.arr.push(b);
                 
            },
      delet:function(i){
               this.arr.splice(i,1)
      },
      show:function(){
        this.ifShowInfo=!this.ifShowInfo
        //this.ifLogin=!this.ifLogin
      },
      chaTo:function(mdValue,inx){
        this.user.mdValue=mdValue;
        index=inx;
        console.log(inx)
        console.log(this.user.mdValue)
      },
      save:function(mdValue,inx){
              this.arr[index].mdValue=buf
               this.user.mdValue=buf
               let postdata=  this.$qs.stringify(   { name:this.user.name,contains:this.arr}    );  
               //console.log(postdata)
             this.$axios.post('https://www.cherryhuang.cn:8080/updata', postdata).then((response) => {
         // console.log(response);
          //console.log(response.data);
          this.postData = response.data;
          if(response.data[0]){
            this.$Message.success('');
          }
        }).catch((error) => {
          console.log(error);
        });


      },
      toR:function(){
        this.toReg=!this.toReg
      },
       toL:function(){
          this.toReg=false
          this.ifLogin=true
      },
      backTo:function(){
        this.toReg=!this.toReg
      },
      LoginSubmit:function(){
        console.log()
        
        
      let url="https://www.cherryhuang.cn:8080/login?name="
      let url1="&&password="
      let name_user=this.formInline.user
      let password=this.formInline.password
      
      console.log(url+name_user+url1+password);
                
  
      this.$axios.get(url+name_user+url1+password)
       .then( (response) =>{
  

         //this.i=
            console.log(response.data)
         if(response.data[0]) {
           this.logged=!this.logged
           this.$Message.info(' '+name_user+' ');
           
          this.ifLogin=!this.ifLogin;
          this.logged=this.logged;
          this.user.name=name_user;
          this.arr=response.data[1];
          
          
          }
         else{
           this.$Message.info(' '+name_user+' ');
            
         }


                    })
                     .catch(function (error) {
                     console.log(error);
                     })





      },
      handleRegiseterSubmit:function(){
        let url="https://www.cherryhuang.cn:8080/register?name="
      let url1="&&password="
      let url2="&&email="
      let name_user=this.formCustom.user
      let password=this.formCustom.passwd
      let email=this.formCustom.email
      console.log(url+name_user+url1+password+email)
            this.$axios.get(url+name_user+url1+password+url2+email)
       .then( (response) =>{

         if(response.data[1]) {
             this.$Message.success('Success!');
             
             
             
             this.toReg=false;}
         else{
                this.$Message.error('Fail! User name has been  occupied!');
         }


                    })
                     .catch(function (error) {
                     console.log(error);
                     })
            },
            handleReset (name) {
                this.toReg=false
            }
  
      }
    }

</script>
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
