   transform: translate3d(0,100%, 0);
}
.loginDonghua-leave{
   transform: translate3d(0, 0, 0);
}
      .left {
        float: left;
        width: 100%;
        height: 100%;
        background-color: #aaaaaa;
        margin-right: -100%;
        margin-bottom: -100%;
        padding: 5%;
      }
      .right {
         opacity: 0.8;
        float: right;
        width: 15%;
         height: 100%;
        background-color: #ccc;
        overflow: auto;
      }
      .middle{
         opacity: 0.8;
       background: aqua;
color:#ffffff;
overflow: auto;
z-index: 9999;
position: fixed;
padding:5px;
text-align:center;
width: 40%;
height: 30%;
border-bottom-left-radius: 4px;
border-bottom-right-radius: 4px;
border-top-left-radius: 4px;	
border-top-right-radius: 4px;
right: 30.9%;
top: 45%;
        
      }
.font{
  padding-top: 8px;
}
.border{
  border-bottom:solid;
  border-bottom-width: 1px;
  border-bottom-color: aqua;
}
</style>



